<template>
  <section class="resume-section p-3 p-lg-5 d-flex flex-column" id="experience">
    <div class="my-auto">
      <app-education :parts="education"/>
      <app-experience :parts="experience"/>
    </div>
  </section>
</template>

<script>
import education from './Education.vue'
import experience from './Experience.vue'

export default {
  data () {
    return {
      education: [
        {
          'school': 'University West',
          'course': 'Network Enginnering & IT- Security',
          'date_started': '2010',
          'date_ended': '2012'
        }
      ],
      experience: [
        {
          'company': 'Volvo Information Technology',
          'position': 'Application Specialist',
          'date_started': '2012',
          'date_ended': '2015',
          'summary': 'Time&Attendance system for Volvo Group and Volvo Cars. JAVA based Applications running on Linux and Windows Servers and Embedded devices.'
        },
        {
          'company': 'Connection Technology Systems Nothern Europe',
          'position': 'Product Manager / Network Engineer',
          'date_started': '2015',
          'date_ended': 'Present',
          'summary': 'Time&Attendance system for Volvo Group and Volvo Cars. JAVA based Applications running on Linux and Windows Servers and Embedded devices.'
        }
      ]
    }
  },
  components: {
    appEducation: education,
    appExperience: experience
  }
}
</script>

<style>

</style>



